import React, { useEffect, useState } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  Table,
  Button,
} from "reactstrap";
import Axios from "axios";
import Cookies from "js-cookie";
import history from "../../history";
import { Spinner } from "../../component";
import { format } from "date-fns";

export const TransactionDetail = (props) => {
  const [isLoading, setIsLoading] = useState(false);
  const [TransactionDetail, setTransactionDetail] = useState([]);
  const [user, setUser] = useState({
    name: "",
    phone_number: "",
    alamat: "",
    kelurahan: "",
  });

  async function getData() {
    setIsLoading(true);
    const res = await Axios({
      method: "GET",
      url: `https://antarblanja.com/api/admin/transaction/${props.match.params.transaction_id}`,
      headers: { Authorization: Cookies.get("admin_token") },
    });
    setTransactionDetail(res.data.detail);
    setUser(res.data.user);
    console.log(res.data);
    setIsLoading(false);
  }

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="animated fadeIn">
      <Spinner isLoading={isLoading} />
      <Row>
        <Col xs="12" lg="6">
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> TransactionDetail
            </CardHeader>
            {TransactionDetail.length > 0 ? (
              <CardBody>
                <Table responsive>
                  <thead>
                    <tr>
                      <th>item_name</th>
                      <th>total_price</th>
                      <th>total quantity</th>
                    </tr>
                  </thead>
                  <tbody>
                    {TransactionDetail.map((item, index) => {
                      return (
                        <tr key={index}>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.item_name}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.total_price}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.total_qty} {item.satuan_name}
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              </CardBody>
            ) : (
              <p style={{ margin: 20, alignSelf: "center" }}>
                No TransactionDetail Yet
              </p>
            )}
          </Card>
        </Col>
        <Col>
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Customer data
            </CardHeader>
            <CardBody>
              <Table responsive>
                <thead>
                  <tr>
                    <th>name</th>
                    <th>phone_number</th>
                    <th>kelurahan</th>
                    <th>alamat</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td style={{ verticalAlign: "middle" }}>{user.name}</td>
                    <td style={{ verticalAlign: "middle" }}>
                      {user.phone_number}
                    </td>
                    <td style={{ verticalAlign: "middle" }}>
                      {user.kelurahan}
                    </td>
                    <td style={{ verticalAlign: "middle" }}>{user.alamat}</td>
                  </tr>
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default TransactionDetail;
